import Shaderpen from "./shaderpen";
import {
  ImUsBackgroundOptions,
  ImUsBackgroundMessage,
  ShaderpenUniforms,
} from "./types";
import dat from "dat.gui";

const glsl = (x: TemplateStringsArray) => x.toString();

class ImUsBackground {
  pen: any;
  controlledUniforms: ShaderpenUniforms = {
    // Draft settings
    // noise1Speed: { type: "float", value: 1.0 },
    // noise1Motion: { type: "float", value: -2.9 },
    // noise1Scale: { type: "float", value: 0.3 },
    // noise2Speed: { type: "float", value: 1.6 },
    // noise2Motion: { type: "float", value: -4.2 },
    // noise2Scale: { type: "float", value: 0.2 },
    // blurSize: { type: "float", value: 2.3 },
    // padSize: { type: "float", value: 1.4 },

    // Base settings
    noise1Speed: { type: "float", value: 0.3 },
    noise1Motion: { type: "float", value: -2.9 },
    noise1Scale: { type: "float", value: 0.3 },
    noise2Speed: { type: "float", value: 0.3 },
    noise2Motion: { type: "float", value: -4.2 },
    noise2Scale: { type: "float", value: 0.2 },
    blurSize: { type: "float", value: 3.6 },
    padSize: { type: "float", value: 1.4 },

    // New settings
    noise2Mix: { type: "float", value: 1.0 },
  };

  constructor(options: ImUsBackgroundOptions) {
    this.pen = new Shaderpen(
      glsl`
precision highp float;
precision highp int;
#define HIGH_PRECISION
#define GAMMA_FACTOR 2
#define TONE_MAPPING
#pragma vscode_glsllint_stage : frag

uniform vec3 iResolution;
uniform vec3 scaledResolutionTarget;
uniform float iTime;
uniform vec4 DOMColors [20];
uniform vec4 DOM [20];
uniform sampler2D texture1;
uniform vec2 _DitherPattern_TexelSize;
uniform float noise1Speed;
uniform float noise2Speed;
uniform float noise1Motion;
uniform float noise2Motion;
uniform float noise1Scale;
uniform float noise2Scale;
uniform float blurSize;
uniform float padSize;
uniform float noise2Mix;


/**
 * Drawing assumed to be from front to back stage (i.e. closest first):
 * (which allows to stop as soon as opacity is 100% or above some threshold like 99.5%)
 */
vec4 composeFTB(vec4 current, vec4 layer) {
  return current + (1.0 - current.a) * vec4(layer.rgb, 1) * layer.a;
}

/**
 * Drawing assumed to be from back to front stage (i.e. closest last):
 */
vec4 composeBTF(vec4 current, vec4 layer) {
  return mix(current, vec4(layer.rgb, 1.0), layer.a);
}


//  Simplex 3D Noise 
//  by Ian McEwan, Ashima Arts
//
vec4 permute_08(vec4 x) {
    return mod(((x*34.0)+1.0)*x, 289.0);
}
vec4 taylorInvSqrt_08(vec4 r){
    return 1.79284291400159 - 0.85373472095314 * r;
}
float simplex3DNoise(vec3 v){ 
  const vec2  C = vec2(1.0/6.0, 1.0/3.0) ;
  const vec4  D = vec4(0.0, 0.5, 1.0, 2.0);

  // First corner
  vec3 i  = floor(v + dot(v, C.yyy) );
  vec3 x0 =   v - i + dot(i, C.xxx) ;

  // Other corners
  vec3 g = step(x0.yzx, x0.xyz);
  vec3 l = 1.0 - g;
  vec3 i1 = min( g.xyz, l.zxy );
  vec3 i2 = max( g.xyz, l.zxy );

  //  x0 = x0 - 0. + 0.0 * C 
  vec3 x1 = x0 - i1 + 1.0 * C.xxx;
  vec3 x2 = x0 - i2 + 2.0 * C.xxx;
  vec3 x3 = x0 - 1. + 3.0 * C.xxx;

  // Permutations
  i = mod(i, 289.0 ); 
  vec4 p = permute_08( permute_08( permute_08( 
             i.z + vec4(0.0, i1.z, i2.z, 1.0 ))
           + i.y + vec4(0.0, i1.y, i2.y, 1.0 )) 
           + i.x + vec4(0.0, i1.x, i2.x, 1.0 ));

  // Gradients
  // ( N*N points uniformly over a square, mapped onto an octahedron.)
  float n_ = 1.0/7.0; // N=7
  vec3  ns = n_ * D.wyz - D.xzx;

  vec4 j = p - 49.0 * floor(p * ns.z *ns.z);  //  mod(p,N*N)

  vec4 x_ = floor(j * ns.z);
  vec4 y_ = floor(j - 7.0 * x_ );    // mod(j,N)

  vec4 x = x_ *ns.x + ns.yyyy;
  vec4 y = y_ *ns.x + ns.yyyy;
  vec4 h = 1.0 - abs(x) - abs(y);

  vec4 b0 = vec4( x.xy, y.xy );
  vec4 b1 = vec4( x.zw, y.zw );

  vec4 s0 = floor(b0)*2.0 + 1.0;
  vec4 s1 = floor(b1)*2.0 + 1.0;
  vec4 sh = -step(h, vec4(0.0));

  vec4 a0 = b0.xzyw + s0.xzyw*sh.xxyy ;
  vec4 a1 = b1.xzyw + s1.xzyw*sh.zzww ;

  vec3 p0 = vec3(a0.xy,h.x);
  vec3 p1 = vec3(a0.zw,h.y);
  vec3 p2 = vec3(a1.xy,h.z);
  vec3 p3 = vec3(a1.zw,h.w);

  //Normalise gradients
  vec4 norm = taylorInvSqrt_08(vec4(dot(p0,p0), dot(p1,p1), dot(p2, p2), dot(p3,p3)));
  p0 *= norm.x;
  p1 *= norm.y;
  p2 *= norm.z;
  p3 *= norm.w;

  // Mix final noise value
  vec4 m = max(0.6 - vec4(dot(x0,x0), dot(x1,x1), dot(x2,x2), dot(x3,x3)), 0.0);
  m = m * m;
  return 42.0 * dot( m*m, vec4( dot(p0,x0), dot(p1,x1), 
                                dot(p2,x2), dot(p3,x3) ) );
}

float simplexFractal3DNoise(vec3 m) {
	return   0.5333333 * simplex3DNoise(m)
            +0.2666667 * simplex3DNoise(2.0*m)
            +0.1333333 * simplex3DNoise(4.0*m)
            +0.0666667 * simplex3DNoise(8.0*m);
}

// const matrices for 3d rotation
const mat3 rot1 = mat3(-0.37, 0.36, 0.85,-0.14,-0.93, 0.34,0.92, 0.01,0.4);
const mat3 rot2 = mat3(-0.55,-0.39, 0.74, 0.33,-0.91,-0.24,0.77, 0.12,0.63);
const mat3 rot3 = mat3(-0.71, 0.52,-0.47,-0.08,-0.72,-0.68,-0.7,-0.45,0.56);

// directional artifacts can be reduced by rotating each octave
float simplexFractal3DNoise2(vec3 m) {
    return 0.5333333*simplex3DNoise(m*rot1)
      +0.2666667*simplex3DNoise(2.0*m*rot2)
      +0.1333333*simplex3DNoise(4.0*m*rot3)
      +0.0666667*simplex3DNoise(8.0*m);
}


// www.shadertoy.com/view/XsX3zB
// by Nikita Miropolskiy

// discontinuous pseudorandom uniformly distributed in [-0.5, +0.5]^3
vec3 random3_10(vec3 c) {
	float j = 4096.0*sin(dot(c,vec3(17.0, 59.4, 15.0)));
	vec3 r;
	r.z = fract(512.0*j);
	j *= .125;
	r.x = fract(512.0*j);
	j *= .125;
	r.y = fract(512.0*j);
	return r-0.5;
}

// skew constants for 3d simplex functions
const float F3_10 =  0.3333333;
const float G3_10 =  0.1666667;
float simplex3DNoise_10(vec3 p) {
	 // 1. find current tetrahedron T and it's four vertices
	 // s, s+i1, s+i2, s+1.0 - absolute skewed (integer) coordinates of T vertices
	 // x, x1, x2, x3 - unskewed coordinates of p relative to each of T vertices
	 
	 // calculate s and x
	 vec3 s = floor(p + dot(p, vec3(F3_10)));
	 vec3 x = p - s + dot(s, vec3(G3_10));
	 
	 // calculate i1 and i2
	 vec3 e = step(vec3(0.0), x - x.yzx);
	 vec3 i1 = e*(1.0 - e.zxy);
	 vec3 i2 = 1.0 - e.zxy*(1.0 - e);
	 	
	 // x1, x2, x3
	 vec3 x1 = x - i1 + G3_10;
	 vec3 x2 = x - i2 + 2.0*G3_10;
	 vec3 x3 = x - 1.0 + 3.0*G3_10;
	 
	 // 2. find four surflets and store them in d
	 vec4 w, d;
	 
	 // calculate surflet weights
	 w.x = dot(x, x);
	 w.y = dot(x1, x1);
	 w.z = dot(x2, x2);
	 w.w = dot(x3, x3);
	 
	 // w fades from 0.6 at the center of the surflet to 0.0 at the margin
	 w = max(0.6 - w, 0.0);
	 
	 // calculate surflet components
	 d.x = dot(random3_10(s), x);
	 d.y = dot(random3_10(s + i1), x1);
	 d.z = dot(random3_10(s + i2), x2);
	 d.w = dot(random3_10(s + 1.0), x3);
	 
	 // multiply d by w^4
	 w *= w;
	 w *= w;
	 d *= w;
	 
	 // 3. return the sum of the four surflets
	 return dot(d, vec4(52.0));
}

float simplexFractal3DNoise_10(vec3 m) {
	return   0.5333333 * simplex3DNoise_10(m)
            +0.2666667 * simplex3DNoise_10(2.0*m)
            +0.1333333 * simplex3DNoise_10(4.0*m)
            +0.0666667 * simplex3DNoise_10(8.0*m);
}

// const matrices for 3d rotation
const mat3 rot1_10 = mat3(-0.37, 0.36, 0.85,-0.14,-0.93, 0.34,0.92, 0.01,0.4);
const mat3 rot2_10 = mat3(-0.55,-0.39, 0.74, 0.33,-0.91,-0.24,0.77, 0.12,0.63);
const mat3 rot3_10 = mat3(-0.71, 0.52,-0.47,-0.08,-0.72,-0.68,-0.7,-0.45,0.56);

// directional artifacts can be reduced by rotating each octave
float simplexFractal3DNoise2_10(vec3 m) {
    return 0.5333333*simplex3DNoise_10(m*rot1_10)
      +0.2666667*simplex3DNoise_10(2.0*m*rot2_10)
      +0.1333333*simplex3DNoise_10(4.0*m*rot3_10)
      +0.0666667*simplex3DNoise_10(8.0*m);
}


//   _   _  _____ ______  _____  _   _  _____  _____ 
//  | | | ||  _  || ___ |  _  ||  | ||  _  ||_   _|
//  | | | || | | || |_/ /| | | ||  | || | | |  | |  
//  | | | || | | ||    / | | | || . ' || | | |  | |  
//   _/ / _/ /| |   _/ /| |  | _/ / _| |_ 
//   ___/  ___/ _| _| ___/ _| _/ ___/  ___/ 
//                                                   
//                                                   

// by inigo quilez http://iquilezles.org/www/articles/voronoise/voronoise.htm
// https://www.shadertoy.com/view/Xd23Dh
//

vec3 hash3_11( vec2 p ){
    vec3 q = vec3( dot(p,vec2(127.1,311.7)), 
				   dot(p,vec2(269.5,183.3)), 
				   dot(p,vec2(419.2,371.9)) );
	return fract(sin(q)*43758.5453);
}

float voroNoise_iq( in vec2 x, float u, float v ){
    vec2 p = floor(x);
    vec2 f = fract(x);
		
	float k = 1.0+63.0*pow(1.0-v,4.0);
	
	float va = 0.0;
	float wt = 0.0;
    for( int j=-2; j<=2; j++ )
    for( int i=-2; i<=2; i++ )
    {
        vec2 g = vec2( float(i),float(j) );
		vec3 o = hash3_11( p + g )*vec3(u,u,1.0);
		vec2 r = g - f + o.xy;
		float d = dot(r,r);
		float ww = pow( 1.0-smoothstep(0.0,1.414,sqrt(d)), k );
		va += o.z*ww;
		wt += ww;
    }
	
    return va/wt;
}

//	https://www.shadertoy.com/view/lsjGWD
//	by Pietro De Nicola
//
// Use:
// vec2 p = gl_FragCoord.xy/iResolution.xx;
// float c = POWER*fbm( SCALE*p ) + BIAS;
#define OCTAVES_12   		1		// 7
#define SWITCH_TIME_12 	5.0		// seconds

vec2 hash_12( vec2 p ){
	p = vec2( dot(p,vec2(127.1,311.7)),dot(p,vec2(269.5,183.3)));
	return fract(sin(p)*43758.5453);
}

float voronoi_12( in vec2 x, float type, float speed ){
    // When calculating FBM noise, these first variables could be optimized
    // as they are the same for each octave.
    float function_12 			= mod(type, 4.0);
    bool  multiply_by_F1_12	    = mod(type, 8.0)  >= 4.0;
    bool  inverse_12			= mod(type, 16.0) >= 8.0;
    float distance_type_12	    = mod(type/16.0, 4.0);

	vec2 n = floor( x );
	vec2 f = fract( x );
	
	float F1 = 8.0;
	float F2 = 8.0;
	
	for( int j=-1; j<=1; j++ )
		for( int i=-1; i<=1; i++ ){
			vec2 g = vec2(i,j);
			vec2 o = hash_12( n + g );

			o = 0.5 + 0.41*sin(speed * iTime + 6.2831*o );	
			vec2 r = g - f + o;

		float d = 	distance_type_12 < 1.0 ? dot(r,r)  :				// euclidean^2
				  	distance_type_12 < 2.0 ? sqrt(dot(r,r)) :			// euclidean
					distance_type_12 < 3.0 ? abs(r.x) + abs(r.y) :		// manhattan
					distance_type_12 < 4.0 ? max(abs(r.x), abs(r.y)) :	// chebyshev
					0.0;

		if( d<F1 ) { 
			F2 = F1; 
			F1 = d; 
		} else if( d<F2 ) {
			F2 = d;
		}
    }
	
	float c = function_12 < 1.0 ? F1 : 
			  function_12 < 2.0 ? F2 : 
			  function_12 < 3.0 ? F2-F1 :
			  function_12 < 4.0 ? (F1+F2)/2.0 : 
			  0.0;
		
	if( multiply_by_F1_12 )	c *= F1;
	if( inverse_12 )			c = 1.0 - c;
	
    return c;
}

float voronoi_12( in vec2 x ){
    float t = iTime/SWITCH_TIME_12;
    return voronoi_12(x, t, 1.0);
}

float voronoiFBM_12( in vec2 p ){
	float s = 0.0;
	float m = 0.0;
	float a = 0.5;
	
	for( int i=0; i<OCTAVES_12; i++ ){
		s += a * voronoi_12(p);
		m += a;
		a *= 0.5;
		p *= 2.0;
	}
	return s/m;
}

// --- From http://www.madebyevan.com/shaders/fast-rounded-rectangle-shadows/
// License: CC0 (http://creativecommons.org/publicdomain/zero/1.0/)
/**
 * Faster way to draw box shadows and blurred rectangles.
 * Return the mask for the shadow of a box from lower to upper
 * sigma: size of the shadow, ~ in the given unit
 **/ 
// This approximates the error function, needed for the gaussian integral
vec4 erf(vec4 x) {
  vec4 s = sign(x), a = abs(x);
  x = 1.0 + (0.278393 + (0.230389 + 0.078108 * (a * a)) * a) * a;
  x *= x;
  return s - s / (x * x);
}
// Return the mask for the shadow of a box from lower to upper
float boxShadow(vec2 lower, vec2 upper, vec2 point, float sigma) {
  // Nathan Vogel: Optimize by not calculating beyond the blur distance. 
  // 2.5 seems about correct, 2.3 might cut a tiny bit.
  if (point.y < lower.y - sigma * 2.6) return 0.0;
  if (point.y > upper.y + sigma * 2.6) return 0.0;
  if (point.x < lower.x - sigma * 2.6) return 0.0;
  if (point.x > upper.x + sigma * 2.6) return 0.0;
  vec4 query = vec4(point - lower, point - upper);
  vec4 integral = 0.5 + 0.5 * erf(query * (sqrt(0.5) / sigma));
  return (integral.z - integral.x) * (integral.w - integral.y);
}
// --- End of: From madebyevan.com --------------------


vec2 remapDither(vec2 v) {
  return (mod(v, _DitherPattern_TexelSize)) / _DitherPattern_TexelSize;
}

void mainImage( out vec4 fragColor, in vec2 fragCoord )
{
  vec2 coord = fragCoord;
  vec2 baseP = 2.0 * (coord.xy - iResolution.xy) / scaledResolutionTarget.y;

  vec4 color = vec4(1.0);

  for (int i = 0; i < 20; i += 1) {
    float height = DOM[i].y - DOM[i].w;
    // Skip the ones without height (= not bound to a node.)
    if (height == 0.0) { continue; }

    // Attempt to calculate a resonnable blur
    float blur = blurSize * 0.00008 *
        max(scaledResolutionTarget.x, scaledResolutionTarget.y) * 
        min(height, scaledResolutionTarget.y / 2.0);
    // Change the visual FX when its a draft.
    bool draft = DOMColors[i].w >= 1.0;
    if (draft) { blur = blur / 1.57; }

    // Create a fast blurred rectangle (I remapped coordinates through trial and error).
    // This box shadow blur also blurs inside the original rect, so we add some padding 
    // to account for it.
    vec2 pad = vec2(blur * 0.5 * padSize);
    float rect = boxShadow(DOM[i].xw - pad, DOM[i].zy + pad, coord, blur);
    // Optimize by not calculating any noise etc. where it won't be seen.
    if (rect < 3.0/256.0) { continue; }

    vec2 p = baseP / (25.0 + blur) * 800.0;
    vec2 noiseSpeed = -vec2(0.2, 0.7) * iTime;
    float noise = voronoi_12(
      noise1Scale * p * 0.25 + noise1Motion * 1.8 * noiseSpeed, 
      23.0, 
      noise1Speed * 2.0 * (draft ? 3.33 : 1.0)
    ); 
    float noise2 = simplex3DNoise(vec3(
      noise2Scale * p * 0.08 + noise2Motion * 0.5 * noiseSpeed, 
      noise2Speed * 0.3 * iTime * (draft ? 5.0 : 1.0)
    ));
    float mask = mix(noise * rect, rect, rect);
    mask = mask * (1.0 - noise2Mix) + noise2Mix * mix(noise2 * mask, mask, rect);

    // Compose all messages one after the other.
    color = composeBTF(color, vec4(DOMColors[i].xyz, mask));
  }

  fragColor = color;
}

void main() {
  mainImage(gl_FragColor, gl_FragCoord.xy);
}
      `,
      "NO_RENDER",
      Object.assign(
        {
          textures: [],
          midiBindings: [],
          additionalUniforms: this.controlledUniforms,
        },
        options
      )
    );

    if (options.showDatGui) {
      const uniforms = this.pen.uniforms;
      const gui = new dat.GUI();
      gui.add(uniforms.noise1Speed, "value", 0, 5).name("Noise 1 Speed");
      gui.add(uniforms.noise1Motion, "value", -5, 5).name("Noise 1 Motion");
      gui.add(uniforms.noise1Scale, "value", 0, 5).name("Noise 1 Scale");
      gui.add(uniforms.noise2Speed, "value", 0, 5).name("Noise 2 Speed");
      gui.add(uniforms.noise2Motion, "value", -5, 5).name("Noise 2 Motion");
      gui.add(uniforms.noise2Scale, "value", 0, 5).name("Noise 2 Scale");
      gui.add(uniforms.blurSize, "value", 0, 5).name("Blur Scale");
      gui.add(uniforms.padSize, "value", -2, 5).name("Pad Scale");
      gui.add(uniforms.noise2Mix, "value", -0, 1).name("Noise 2 Mix");
    }

    this.pen.render();
  }

  setMessages = (messages: ImUsBackgroundMessage[]) => {
    const bindings = messages.map((message) =>
      Object.assign({ flags: message.draft ? 1 : 0 }, message)
    );
    this.pen.setDOMBindings(bindings);
  };
}

export default ImUsBackground;
