import React, { useEffect } from "react";
import ImUsBackground from "./ImUsBackground";
import { ImUsBackgroundMessage } from "./types";

function App() {
  useEffect(() => {
    const background = new ImUsBackground({
      midiBindings: [
        // [176, 48, { from: 0, to: 10, initial: 1.0 }],
        // [176, 49, { from: 0, to: 10, initial: 1.0 }],
        // [176, 50, { from: 0, to: 10, initial: 1.0 }],
        // [176, 51, { from: 0, to: 10, initial: 1.0 }],
        // [176, 52, { from: -2, to: 2, initial: 1.0 }],
        // [176, 53, { from: -2, to: 2, initial: 1.0 }],
        // [176, 54, { from: -2, to: 2, initial: 1.0 }],
        // [176, 55, { from: -2, to: 2, initial: 1.0 }],
      ],
      midiLogs: false,
      showDatGui: true,
      maxDomNodeCount: 20,
      canvas: document.getElementById("background") as HTMLCanvasElement,
    });

    const messages: ImUsBackgroundMessage[] = [];
    const elements = document.getElementsByClassName("message");
    for (let i = 0; i < elements.length; i += 1) {
      messages.push({
        element: elements[i],
        color: [i / elements.length, Math.random(), Math.random()],
        draft: elements[i].classList.contains("draft"),
      });
    }
    console.log(messages);
    background.setMessages(messages);
  }, []);

  return (
    <div id="ui">
      <div
        contentEditable="true"
        suppressContentEditableWarning={true}
        className="message big draft"
      >
        Type here...
      </div>
      <div className="message big">#StayHome</div>
      <div className="message big">This is not a drill.</div>
      <div className="message big">Culture is technoculture.</div>
      <div className="message big">
        Arguing that you don't care about the right to privacy because you have
        nothing to hide is no different than saying you don't care about free
        speech because you have nothing to say.
      </div>
      <div className="message big">Why are people still going outside?</div>
      <div className="message">The streets are empty...</div>
      <div className="message">Hi</div>
      <div className="message">The city is so quiet.</div>
      <div className="message">
        Real, total war has become information war. It is being fought by
        subtle electric informational media- under cold conditions, and
        constantly. The cold war is the real war front- a surround- involving
        everybody- all the time- everywhere. Whenever hot wars are necessary
        these days, we conduct them in the backyards of the world with the old
        technologies. These wars are happenings, tragic games. It is no longer
        convenient, or suitable, to use the latest technologies for fighting our
        wars, because the latest technologies have rendered wars meaningless.
        The hydrogen bomb is historys exclamation point.
      </div>
      <div className="message big">
        I arise in the morning torn between a desire to improve (or save) the
        world and a desire to enjoy (or savor) the world.
      </div>
      <div className="message big">I'm #StayingAtHome</div>
    </div>
  );
}

export default App;
